.Level_Container{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    width: auto;
    height: calc(100vh - 58px);
    position: relative;
}

/*  ███ Active Area     ███ */

.Active{
    flex-grow: 1;    
    height: 100%;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    

}

.TestButton{
    --size: 200px;
    width: var(--size);
    height: var(--size);
    border-radius: 40%;

    cursor: pointer;
}

/*  ███ Narrative Area  ███ */

.Narrative{ 
    background-color: var(--accent-color-light);

    width: 420px;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    color: var(--primary-color);
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
    
}

.Animation{

    background-color: var(--accent-color-dark);
    width: 400px;
    height: 400px;
    margin: 10px;
    margin-bottom: 12px;

    padding: 8px;
}

.Timer{

    background-color: var(--accent-color-dark);
    width: 400px;
    height: 60px;
    margin-bottom: 12px;
    
    padding: 8px;
}

.Narration{
    -webkit-transition: color .5s ease-in-out;
    -moz-transition: color .5s ease-in-out;
    -ms-transition: color .5s ease-in-out;
    -o-transition: color .5s ease-in-out;
    transition: color .5s ease-in-out;

    background-color: var(--accent-color-dark);
    width: 400px;
    flex: 1;
    margin-bottom: 12px;
    
    padding: 8px;
}

/*  ███ Grayscreen Continue ███ */
.Grayscreen{
    position: absolute;
    width: calc(100% - 420px);
    height: 100%;
    background-color: rgba(0, 0, 0, 0.64);
    top: 0;
    left: 0;

    display: none;
    
    justify-content: center;
    align-items: center;
}
.Continue {
    --Link-Padding: 8px;

    background-color: var(--accent-color-dark);
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 8px;
    border-radius: 20px;
    border-style: solid;
    border-color: var(--primary-color);
    border-width: 6px;

    height: 80px;
    width: 160px;

    color: var(--primary-color);
    font-size: x-large;
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}


/*  ███ Additional Styling ███ */

.BB{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

#GB{
    display: block;
}
#GBP{
    display: none;
}
#RB{
    display: block;
}
#RBP{
    display: none;
}