.navbar-container{
    --Nav-Height: 58px;
    background-color: var(--secondary-color);

    height: var(--Nav-Height);
    width: 100%;
    padding-right: var(--Nav-Height);
    padding-left: var(--Nav-Height);

    display: flex;
    justify-content: space-between;

    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}

/*  ███ Home Link       ███ */

.HomeLink{
    --Home-Margin: 20px;
    --Home-Padding: 8px;

    display:flex;

    padding: var(--Home-Padding);
    padding-left: 0;
    color: var(--primary-color);
    font-size: 14px;
}

.Logo{
    height: 40px;
}

.Logo-Title{
    margin: auto;
    padding: 0;
    padding-left: 10px;
    color: var(--primary-color);
    font-size: large;
}

/*  ███ Quick Links     ███ */

.Link_Container{
    display: flex;
    flex-direction: row;
    gap: 8px;
    padding: 8px;
    padding-right: 0;

    font-size: large;
}

.Link{
    --Link-Padding: 8px;

    background-color: var(--accent-color-dark);
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 8px;
    border-radius: 5px;

    color: var(--primary-color);
}



.BB{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}