.Active_1{
    flex-grow: 1;    
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    

}

.ActiveTitle {
    font-size: 50px;
    font-weight: 900;

    color: var(--secondary-color);
}


.Red {
    background-color: lightcoral;
}
.Blue {
    background-color: lightskyblue;
}
.Green {
    background-color: lightseagreen;
}
.lol {
    width: 10px;
    height: 10px;
    background-color: aqua;
}
#Grayscreen{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.90);
    top: 0;
    left: 0;
}
.start{
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    width: 100%;
    height: 52px;
    background-color: white;
    text-align: center;

    cursor: pointer;
}

.NextContainer {
    display: none;

    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 420px);
    height: 100%;

    background-color: rgba(0, 0, 0, 0.473);
}

#Img2 {
    display: none;
}